.swiper__button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 3rem;
  height: 3rem;

  color: $color-gray--800;
  border-radius: 50%;

  transition: background-color .4s ease-out;

  &--next {
    transform: scale(-1, 1);
  }

  &:hover,
  &:focus {
    background-color: $color-primary;

    .swiper__icon {
      fill: $color-white;
    }
  }
}

.swiper__icon {
  fill: currentColor;
  transition: fill .4s ease-out;
}
