.course-card {
  display: flex;

  height: 100%;
  margin: 0 auto;

  text-decoration: none;

  background-color: $color-white;
  border: 1px solid #E5E8ED;
  border-radius: 4px;

  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);

  transition: box-shadow .3s ease-out;

  &:hover,
  &:focus {
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.16), 0px 30px 24px -10px rgba(154, 156, 165, 0.1), 0px 12px 10px -6px rgba(154, 156, 165, 0.08), 0px 4px 4px -4px rgba(30, 33, 44, 0.05);
  }

  &--horizontal {
    align-items: flex-start;

    .course-card__photo {
      min-width: 213px;
      width: 213px;
      min-height: 210px;

      object-fit: cover;
    }

    @include media-max-mobile {
      flex-direction: column;

      max-width: 214px;
    }
  }

  &--vertical {
    flex-direction: column;

    max-width: 390px;
  }
}

.course-card__content {
  height: 100%;

  padding: 1.8rem;
}

.course-card__title {
  margin-bottom: 1.25rem;

  @include font(1.25rem, 150%, 700);
  color: $color-gray--900;
}

.course-card__price {
  @include font(1.125rem, 150%, 700);
  color: $color-red;
}

.course-card__author {
  @include font(1.125rem, 150%, 400);
  color: $color-gray--700;
}
